

















import Vue from "vue";
import NumberPad from "@/components/Money/NumberPad.vue";
import FormItem from '@/components/Money/FormItem.vue';
import Tags2 from "@/components/Money/Tags2.vue";
import { Component,Watch } from "vue-property-decorator";
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import { Notify } from "vant";

@Component({
  components: { Tags2, FormItem, Tabs, NumberPad },
})
export default class Money extends Vue {
  get recordList() {
    return this.$store.state.recordList;
  }
  public $refs!:{
    tags:Tags2
  }
  recordTypeList = recordTypeList;
  
  record: RecordItem = {
    tags: [],
    notes: "",
    type: "-",
    amount: 0,
    createdAt: ''
  };

  created(){
    this.$store.commit('fetchRecords');
  }
  
  onUpdateTags(value:any){
    this.record.tags.splice(0,1,...value[0]);
  }

  saveRecord(amount1:any,createdAt1:any) {
    var str = new Date().toISOString()
    this.record.createdAt = createdAt1 + str.substring(10)
    if(this.record.tags.length===0){
        Notify({ type: 'warning', message: '请选择一个标签！',
            duration:1000,});
    }else{
      this.$store.commit('createRecord',this.record);
      this.record.notes='';
      this.$refs.tags.active = -1
    }
  }

}
