





























import { Notify } from 'vant';
import Vue from 'vue';
import {Component} from 'vue-property-decorator';

@Component({})
export default class Label extends Vue {
    get tags(){
      return this.$store.state.tagList;
    }
    rSelect:string[]=[];
    rSelect2:string[]=[];
    detail= [{
            title:'公交',
            children:[
                {type:'car',name:'公交'},
                {type:'taxi',name:'出租'},
                {type:'plane',name:'飞机'},
                {type:'train',name:'火车'},
                {type:'subway',name:'地铁'}
            ]
        },{
            title:'餐饮',
            children:[
                {type:'foot',name:'餐饮'},
                {type:'food1',name:"零食"},
                {type:"food2",name:'外卖'},
                {type:'food3',name:'买菜'},
                {type:"food4",name:'酒水'}
            ]
        },{
            title:'娱乐',
            children:[
                {type:"play2",name:'娱乐'},
                {type:'play',name:"游戏"},
                {type:'movie',name:'电影'},
                {type:'run',name:'运动'},
                {type:'traveling',name:"旅游"}
            ]
        },{
            title:'购物',
            children:[
                {type:"shopping",name:'购物'},
                {type:'clothes',name:'衣服'},
                {type:'cosmetics',name:"化妆品"},
                {type:'digital',name:'数码'}
            ]
        },{
            title:'生活居住',
            children:[
                {type:'stay',name:'生活居住'},
                {type:'daily',name:'生活用品'},
                {type:'hcost',name:'话费'},
                {type:'lottery',name:'彩票'},
                {type:'scost',name:'水电费'}
            ]
        },{
            title:"医疗",
            children:[
                {type:"medical",name:'医疗'},
                {type:'drug',name:'药品'},

            ]
        }
    ]
    ok(){       
        this.rSelect = [];
        this.$router.push({path:'/money'});
        this.$store.commit('createTag',this.rSelect2)
        this.rSelect2=[];
    }
    selected(type:any,item1:any,title:any){
        const tagTypes = this.tags.map((item: { type: any; })=> item.type)
        if(tagTypes.indexOf(type)>=0){
            Notify({ type: 'warning', message: '标签已经添加过了！',
                duration:1000,});
            return
        }
        if(this.rSelect.indexOf(type)!==-1){
            this.rSelect.splice(this.rSelect.indexOf(type),1)//再次选择取消
            this.rSelect2.splice(this.rSelect2.indexOf(type),1)
        }else{
            if(this.rSelect.length<5){
                this.rSelect.push(type);
                let key = 'title';
                item1[key] = title;
                this.rSelect2.push(item1)
            }else{
                Notify({ type: 'warning', message: '最多一次只能添加5个标签!',
                duration:1000,});
            }
        }
    }
    goBack(){
      this.$router.back();
    }
}
